import "../authentication/authentication.css";
import GoogleBtn from "../../components/Social-btn/GoogleBtn";
import FacebookBtn from "../../components/Social-btn/FacebookBtn";
import FormInput from "../../components/shared/formInput";
import { useState, useEffect, useCallback } from "react";
import { userSlice } from "../../redux/User";
import _ from "lodash";
import { State } from "../../redux/Types";
import { useDispatch, useSelector } from "react-redux";
import { axiosPost } from "../../services/axiosService";
import { setCookie } from "../../services/cookieService";
import { Link } from "react-router-dom";
import environment from "../../constants/Environment";
import { useNavigate, useLocation } from "react-router-dom";
import validateData from "../../services/validationService";
import Loader from "../../components/loaders/Loader";
import { AiChatBotShortLogo } from "../../assets/AiBotAssets";
import * as GtmService from "../../services/gtmService";
import { Crisp } from "crisp-sdk-web";

function SignUp() {
  const route = useNavigate();
  const dispatch = useDispatch();
  const location = useLocation();
  const environmentData = environment();
  const addon = new URLSearchParams(location.search).get("addon");
  const queryEmail = new URLSearchParams(location.search).get("email");
  const queryCode = new URLSearchParams(location.search).get("code");
  const quesryAmount = new URLSearchParams(location.search).get("price");
  const calcRedirect = new URLSearchParams(location.search).get("calc");
  const isLoggedIn = useSelector((state: State) => state.user.isLoggedIn);
  const couponRedirect = new URLSearchParams(location.search).get("lifetime");
  const planCodes = new URLSearchParams(location.search).get("plan_code");
  const path = window.location.pathname;
  const [email, setEmail] = useState("");
  const [code, setCode] = useState("");
  const [price, setPrice] = useState("");
  const [password, setPassword] = useState("");
  const [company, setCompany] = useState("");
  const [planCode, setPlanCode] = useState("");
  const [coupon, setCoupon] = useState("");
  const [checked, setChecked] = useState(true);
  const [subdomain, setSubdomain] = useState("");
  const [emailErrMsg, setEmailErrMsg] = useState(false);
  const [passwordErrMsg, setPasswordErrMsg] = useState(false);
  const [companyErrMsg, setCompanyErrMsg] = useState(false);
  const [isCouponValid, setIsCouponValid] = useState<boolean>();
  const [loader, setLoader] = useState(false);
 

  useEffect(() => {
    if (isLoggedIn) {
      if (planCodes?.length) {
        route(`/settings?code=${planCode}&addon=false&price=${price}`)
        dispatch(userSlice.actions.setSettings(2));
        // axiosPost(`/payments/hostedPage`, body)
        //   .then((response) => {
        //     window.location.assign(response.data.url);
        //   })
        //   .catch((err) => {
        //     console.log(err);
        //   });
      }
      else if (calcRedirect) {
        
        route(`/settings?code=${code}&price=${price}&addon=${addon}`)
        dispatch(userSlice.actions.setSettings(2));
        // addons?.delete('addon');
        // route(`../register?calc=false&email=${email}&addon=${addon}`, {
        //   replace: true,
        // });

        // axiosPost(`/payments/calcHostedPage`, body)
        //   .then((response) => {
        //     window.location.assign(response.data.url);
        //   })
        //   .catch((err) => {
        //     console.log(err);
        //   });

        // route(`/calculator?addon=${addon}`);
      } else if (couponRedirect) {
        route("/lifetime");
      } else {
        route("/onboarding");
      }
    }
  }, [isLoggedIn, subdomain, planCodes, calcRedirect, couponRedirect]);

  
  useEffect(() => {
    if (queryEmail && queryEmail.trim().length >= 1) {
      if (validateData(queryEmail, "email")) {
        setEmailErrMsg(false);
        setEmail(queryEmail as string);
        setPrice(quesryAmount as string);
        setCode(queryCode as string);
      } else {
        setEmailErrMsg(true);
      }
    }else if(planCodes && planCodes.trim().length > 1){
      setPlanCode(planCodes)
      setPrice(quesryAmount as string);
    }
  }, [queryEmail,planCodes]);

  

  useEffect(() => {
    const validEmail = validateData(email, "email");
    const validPassword = validateData(password, "password");
    const validCompany = validateData(company, "company");

    if (validEmail || validPassword || validCompany) {
      setEmailErrMsg(false);
      setPasswordErrMsg(false);
      setCompanyErrMsg(false);
    }
  }, [email, password, company, coupon]);

  const validateEmailOnBlur = () => {
    const validEmail = validateData(email, "email");
    setEmailErrMsg(!validEmail);
  };

  const validatePasswordOnBlur = () => {
    const validPassword = validateData(password, "password");
    setPasswordErrMsg(!validPassword);
  };

  const validateCompanyOnBlur = () => {
    const validCompany = validateData(company, "company");
    setCompanyErrMsg(!validCompany);
  };

  const handleDebounce = useCallback(
    _.debounce(async (val) => {
      const body = {
        coupon_code: val,
        plan_code: "lifetime_01",
        sign_up: true,
      };
      try {
        const res = await axiosPost(`/payments/validateCoupon`, body);
        setIsCouponValid(res.data.success);
      } catch (err) {
        console.log(err);
      }
    }, 1000),
    [],
  );

  async function handleSignUp() {
    try {
      if (loader) return;

      const validEmail = validateData(email, "email");
      const validPassword = validateData(password, "password");
      const validCompany = validateData(company, "company");

      setEmailErrMsg(!validEmail);
      setPasswordErrMsg(!validPassword);
      setCompanyErrMsg(!validCompany);

      if (!validEmail || !validPassword || !validCompany) return;

      if (!email || !password || !company || !checked)
        throw new Error("All fields are required");
      setLoader(true);

      const response = await axiosPost("/auth/signup", {
        email: email.trim(),
        password: password.trim(),
        company: company.trim().replace(/\s/g, "-"),
        coupon: coupon.trim(),
      });
      const { userData, companyData, thriveData } = response.data.data;

      setCookie("onBoarding", "true", 1, environmentData?.PUBLIC_DOMAIN, "/");
      setCookie(
        `journey-builder:${userData._id}`,
        "true",
        2,
        environmentData?.PUBLIC_DOMAIN,
        "/",
      );
      setCookie(
        `journey-dashboard:${userData._id}`,
        "true",
        2,
        environmentData?.PUBLIC_DOMAIN,
        "/",
      );

      Crisp.setTokenId(userData._id);
      Crisp.session.reset();
      Crisp.user.setEmail(userData.userEmail);

      dispatch(
        userSlice.actions.login({
          userId: userData._id,
          userEmail: userData.userEmail,
          userName: userData.userName,
          companyDetails: companyData,
        }),
      );

      // dispatch(userSlice.actions.setThriveData({
      //   email: thriveData.userEmail,
      //   digest : thriveData.hmacId,
      //   userId : thriveData.userEmail.split(/[^a-zA-Z0-9]+/)[0]
      // }))
      setSubdomain(companyData.subDomain);
      setLoader(false);

      const manualThriveWidgetLoad = (thriveData) => {
        var trackingScript = document.createElement("script");
        trackingScript.type = "text/javascript";
        trackingScript.text = `
        var ztUserData = {};
        ztUserData['za_email_id'] = '${thriveData?.userEmail}';
        ztUserData['user_unique_id'] = '${thriveData?.userEmail.split("@")[0]}';
        ztUserData['thrive_digest'] = '${thriveData?.hmacId}';
        ztUserData['thrive_ref_id'] = '${localStorage.getItem(
          "thrive_ref_id",
        )}';
        ztUserData['signUpPage'] = 'https://app.outgrow.chat/register';
        ztUserData['signInPage'] = 'https://app.outgrow.chat/login';`;
        document.head.appendChild(trackingScript);
        console.log("lohgin script", trackingScript);
        if (typeof window.reloadThriveWidget === "function")
          window.reloadThriveWidget();
      };
      manualThriveWidgetLoad(thriveData);
      GtmService.pushToDataLayer({
        event: "sign_up",
        user_id: userData._id,
        user_email: userData.userEmail,
        company_id: companyData._id,
        company_name: companyData.compName,
        sub_domain: companyData.subDomain,
      });

      if (environmentData?.isAI) {
        const response = await axiosPost("/users/createBot", {
          projectName: "My first bot",
          botType: "ai",
        });
        const { _id, projectName, botCustomId, url } = response.data.data;
        localStorage.setItem("currentBotId", _id);
        localStorage.setItem("botCustomId", botCustomId);
        localStorage.setItem("botName", projectName);
        await axiosPost("/audience/lists", {
          name: projectName,
          list_id: botCustomId,
          type: "bot",
        });
        route(`/builder/${url}`);
      }
    } catch (err: any) {
      console.log(err);
      dispatch(
        userSlice.actions.setInfoModal({
          showModal: true,
          modalType: "error",
          modalTitle: "Oops!",
          modalMessage:
            err.response?.data.message ?? err.message ?? "Something went wrong",
        }),
      );
      setLoader(false);
    }
  }

  return (
    <div>
      <div className="main_container flex-cnt padding-block">
        <div className="omni-logo">
          {environment().isAI ? (
            <AiChatBotShortLogo />
          ) : (
            <svg
              width="54"
              height="54"
              viewBox="0 0 68 68"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M45.3741 30.4054C47.512 29.4679 50.0516 30.4409 50.5457 32.7226C51.2867 36.1448 51.1705 39.7205 50.176 43.1238C48.7834 47.8892 45.7576 52.0123 41.6292 54.7701C37.5008 57.5279 32.5334 58.7443 27.5979 58.206C22.6624 57.6677 18.0739 55.4091 14.6371 51.8262C11.2003 48.2433 9.13443 43.5649 8.80187 38.6113C8.46931 33.6577 9.89124 28.7451 12.8183 24.735C15.7454 20.7248 19.9908 17.8732 24.81 16.68C28.2517 15.8279 31.8292 15.8605 35.2176 16.7432C37.4767 17.3317 38.3433 19.9096 37.3177 22.0067V22.0067C36.292 24.1038 33.7439 24.891 31.4266 24.6081C29.9056 24.4223 28.3519 24.5123 26.8418 24.8862C23.9502 25.6021 21.403 27.3131 19.6468 29.7192C17.8905 32.1253 17.0373 35.0728 17.2369 38.045C17.4364 41.0171 18.6759 43.8242 20.738 45.9739C22.8001 48.1237 25.5532 49.4788 28.5145 49.8018C31.4758 50.1248 34.4563 49.3949 36.9333 47.7403C39.4103 46.0856 41.2258 43.6118 42.0613 40.7525C42.4977 39.2592 42.6523 37.7107 42.5299 36.1832C42.3436 33.8562 43.2361 31.3429 45.3741 30.4054V30.4054Z"
                fill="#FB5F66"
              ></path>
              <path
                d="M53.1185 23.6639C52.3799 24.2422 50.1861 23.4112 47.814 22.3194C45.9231 23.0419 44.7003 22.6322 47.2581 25.3863C49.1221 27.3934 43.7167 27.1529 40.5963 30.2732C36.5793 34.4384 38.3334 43.7882 33.3978 35.9912C31.538 33.0532 34.1797 42.0226 32.9266 42.3874C31.2648 42.6672 31.4684 40.6416 30.8727 35.8983C30.5888 33.6373 30.2527 35.6886 29.8491 38.1711C31.5761 42.301 31.0061 39.5987 29.7744 41.0952L29.4155 40.6893C29.1159 42.237 28.7841 43.3955 28.4158 43.0272C27.5652 42.1765 26.7057 37.7753 25.551 36.3197L25.3955 36.144C25.4479 36.1964 25.4997 36.2552 25.551 36.3197L29.4155 40.6893C29.5682 39.9008 29.7125 39.0113 29.8491 38.1711C29.3228 36.9127 28.5833 35.0199 27.5843 32.1807C26.5209 27.9698 33.7239 30.3348 36.6549 27.996C39.586 25.6572 40.7038 22.6146 40.2445 19.5203C40.3024 18.4608 44.2585 20.6829 47.814 22.3194C48.4192 22.0881 49.0928 21.7408 49.7329 21.1367C50.1888 20.7063 50.5495 20.2429 50.7828 19.7461L46.7626 13.0023C50.7998 15.6984 51.6303 17.9421 50.7828 19.7461L53.1185 23.6639Z"
                fill="#444444"
              ></path>
              <circle
                cx="47.0261"
                cy="20.4576"
                r="7.51467"
                transform="rotate(-135 47.0261 20.4576)"
                fill="#444444"
              ></circle>
              <circle
                cx="30.155"
                cy="37.3282"
                r="9.0176"
                transform="rotate(-135 30.155 37.3282)"
                fill="#444444"
              ></circle>
            </svg>
          )}
          <h1>{environment().isAI ? "Chatbot Builder" : "Omniengage"}</h1>
        </div>
        <div className="right_container">
          <div className="login-container signUp">
            <div className="head-social-btn ">
              <div className="head">
                <h1>
                  Welcome to{" "}
                  {environment().isAI ? "Chatbot Builder" : "Omniengage"}
                </h1>
                <span>Start your 14 day free trial</span>
                {/* <p>Enter your credentials to create your account</p> */}
              </div>

              <div className="social-btn">
                <GoogleBtn />
                <FacebookBtn />
              </div>
            </div>
            <div className="or-container">
              <div className="line-separator"></div>
              <div className="or-label">or</div>
              <div className="line-separator"></div>
            </div>

            <FormInput
              labelName={"Email"}
              placeHolder={"Please enter email"}
              inputType={"text"}
              isRequired={true}
              value={email}
              onDataChange={(e) => setEmail(e.target.value.trim())}
              onBlur={validateEmailOnBlur}
              onKeyDown={handleSignUp}
            />
            {emailErrMsg && (
              <p className="message">
                <svg
                  width="11"
                  height="11"
                  viewBox="0 0 11 11"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09603 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802V9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099V7.24099Z"
                    fill="#E31E26"
                  />
                </svg>
                {email.length > 0 ? "Invalid Email" : "Field is required"}
              </p>
            )}

            <FormInput
              labelName={"Password"}
              inputType={"password"}
              placeHolder={"Create password"}
              isRequired={true}
              onDataChange={(e) => setPassword(e.target.value.trim())}
              span={""}
              page={"signup"}
              onBlur={validatePasswordOnBlur}
              onKeyDown={handleSignUp}
            />
            {passwordErrMsg && (
              <p className="message">
                <svg
                  width="11"
                  height="11"
                  viewBox="0 0 11 11"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09603 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802V9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099V7.24099Z"
                    fill="#E31E26"
                  />
                </svg>
                {password.length > 0 ? "Invalid Password" : "Field is required"}
              </p>
            )}

            <FormInput
              labelName={"Company"}
              inputType={"text"}
              isRequired={true}
              placeHolder={"Enter your company name"}
              value={company}
              onDataChange={(e) => setCompany(e.target.value)}
              onBlur={validateCompanyOnBlur}
              onKeyDown={handleSignUp}
            />

            {companyErrMsg && (
              <p className="message">
                {password.length > 0
                  ? "Invalid company name. Company name should must be within 3 to 75 characters and should not contain special characters"
                  : "Field is required"}
              </p>
            )}

            {path === "/ltd" && (
              <FormInput
                labelName={"Promo Code"}
                inputType={"text"}
                isRequired={true}
                placeHolder={"LTD promocode"}
                value={coupon}
                onDataChange={(e) => {
                  setCoupon(e.target.value);
                  handleDebounce(e.target.value);
                }}
                onKeyDown={handleSignUp}
              />
            )}
            {isCouponValid === false && (
              <p className="message">
                <svg
                  width="11"
                  height="11"
                  viewBox="0 0 11 11"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10.655 6.23254L7.98647 1.47325C6.89991 -0.464622 4.10224 -0.465462 3.01527 1.47325L0.346758 6.23254C-0.657973 8.02464 0.641496 10.2373 2.69258 10.2373H8.30915C10.3812 10.2373 11.6489 8.00526 10.655 6.23254ZM8.30915 9.37824H2.69258C1.28586 9.37824 0.418764 7.86057 1.09602 6.65266L3.76454 1.89337C4.52362 0.53952 6.47794 0.539185 7.2372 1.89337L9.90571 6.65266C10.5837 7.86183 9.71453 9.37824 8.30915 9.37824ZM5.94663 3.00829V5.54244C5.94663 5.77969 5.75436 5.97196 5.51711 5.97196C5.27994 5.97196 5.08759 5.77969 5.08759 5.54244V3.00829C5.08759 2.77113 5.27994 2.57877 5.51711 2.57877C5.75436 2.57877 5.94663 2.77113 5.94663 3.00829ZM5.93043 7.23904V7.66142C5.93043 7.89859 5.73808 8.09086 5.50091 8.09086C5.26366 8.09086 5.07139 7.89859 5.07139 7.66142V7.23904C5.07139 7.00179 5.26366 6.80952 5.50091 6.80952C5.73808 6.80952 5.93043 7.00179 5.93043 7.23904Z"
                    fill="#E31E26"
                  />
                </svg>
                Invalid Coupon Entered
              </p>
            )}
            <div className="checkbox">
              <div className="checkbox-container">
                <input type="checkbox" checked={checked} />
                <span
                  className="checkmark"
                  onClick={() => setChecked(!checked)}
                ></span>
                <span>
                  By signing up you agree to
                  {environment().isAI ? " ChatbotBuilder" : " Omniengage's"}
                  <a
                    href={
                      environment().isAI
                        ? "https://chatbotbuilder.net/terms-&-conditions.html"
                        : "https://omniengage.co/terms-&-conditions.html"
                    }
                    className="terms-and-conditions-link"
                    // target={environment().isAI ? "_blank" : "_blank"}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    terms and conditions.
                  </a>
                </span>
              </div>
            </div>
            <button
              onClick={handleSignUp}
              disabled={
                path === "/appsumo"
                  ? !email ||
                    !password ||
                    !company ||
                    !checked ||
                    loader ||
                    !coupon ||
                    !isCouponValid
                  : !email || !password || !company || !checked || loader
              }
              type="submit"
              value="signup"
              className="btn"
              id="sign-up"
            >
              {loader ? "Loading..." : "Sign up"}
            </button>
            <p>
              Already a member?<Link to="/">Log in</Link>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SignUp;
